@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Texturina&display=swap);
*{margin:0;padding:0}Body{background:#0c0c53;background:linear-gradient(0deg, #103042 0%, #0c0c53 100%);color:white;font-family:'Akaya Telivigala', cursive}li{list-style:none}h1,h2,h3{font-family:Texturina;letter-spacing:0.1em}a,a:visited,a:hover,a:link,a:active{text-decoration:none;color:white}


.Entete{position:-webkit-sticky;position:sticky;z-index:50;top:0;height:40px;margin:0;padding:10px;font-size:1.2em;background-color:black;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;box-shadow:0px 0px 20px 10px #000000}.Entete p{transition:all 0.5s}.Entete li:hover p{-webkit-transform:scale(1.3);transform:scale(1.3);color:#00a37d}.Entete .navPrincipale{padding:0 0 0 5%;list-style:none;display:-webkit-flex;display:flex;margin:0 auto;font-size:1.2em}.Entete .navPrincipale li{cursor:pointer}.Entete .navPrincipale li:not(:last-of-type){padding-right:15px;border-right:1px solid white}.Entete .navPrincipale li:not(:first-of-type){padding-left:15px}.Entete .langue{position:relative;display:-webkit-flex;display:flex;right:30px;font-size:0.9em}.Entete .langue li{list-style:none}.Entete .langue li button{background-color:rgba(0,0,0,0);border:none;color:white;font-family:'Akaya Telivigala', cursive;font-size:1em;cursor:pointer}.Entete .langue li:not(:last-of-type){padding:0 15px;border-right:1px solid white}.Entete .langue li:not(:first-of-type){padding:0 15px}.Entete .langue .lit{border-radius:50%;cursor:default;color:#000;background:#232323;text-shadow:0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18}

.Banniere{display:-webkit-flex;display:flex;margin:0 auto;-webkit-flex-flow:row wrap;flex-flow:row wrap;-webkit-justify-content:center;justify-content:center}.Banniere .imageBanniere{display:-webkit-flex;display:flex;margin:40px 0;-webkit-justify-content:center;justify-content:center}.Banniere .imageBanniere img{width:80%;box-shadow:0px 0px 20px 10px #000000;transition:all 0.5s}.Banniere .info{margin-bottom:60px;text-align:center}@media screen and (max-width: 700px){.Banniere .imageBanniere img{width:100%}}

.ConteneurProjets{margin:0 auto;width:65%;background-color:#555;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;text-align:center;box-shadow:0px 0px 20px 10px #000000;border-radius:5px;transition:all 0.5s}@media screen and (max-width: 700px){.ConteneurProjets{width:91%}}

.ListeProjets{display:-webkit-flex;display:flex;-webkit-flex-flow:column wrap;flex-flow:column wrap;margin:20px 40px 40px}.ListeProjets h2{margin:20px 0 20px;padding:5px 0;border-bottom:5px solid #00A37D;border-top:5px solid #00A37D;border-radius:20%}.ListeProjets ul{list-style:none;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-evenly;justify-content:space-evenly;margin:0;padding:0}

.Projet{margin:20px;width:400px;height:400px;box-shadow:0px 0px 11px 5px #00a37d;-webkit-align-items:center;align-items:center;border-radius:5px;background-color:#333}.Projet .image{position:-webkit-sticky;position:sticky;z-index:1;width:400px;height:300px;overflow:hidden;cursor:pointer}.Projet .image img{width:400px;height:300px;object-fit:contain;background-color:#14213D;border-radius:5px 5px 0 0}.Projet .image .description{position:relative;padding:15px;text-align:justify;transition:all 0.5s;background-color:rgba(0,0,0,0.8);font-size:1.3em}.Projet .image:hover .description{-webkit-transform:translateY(-100%);transform:translateY(-100%)}.Projet .info{height:25%;display:-webkit-flex;display:flex;-webkit-flex-flow:column wrap;flex-flow:column wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.Projet .noPointer{cursor:default}@media screen and (max-width: 700px){.Projet{width:300px;height:300px}.Projet .image{width:300px;height:200px}.Projet .image img{width:300px;height:200px}.Projet .image .description{font-size:1em}}

.Footer{margin:0 auto;height:40px;width:50%;margin-top:50px;border-radius:100px 100px 0 0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;background:#00a37d;background:linear-gradient(0deg, #00a37d 0%, #14213d 100%);box-shadow:0px 0px 10px 5px #000000}.Footer span{margin:0;padding:0;height:24px;cursor:pointer;transition:all 0.5s}.Footer span:hover{-webkit-transform:scale(1.3);transform:scale(1.3)}

